import React, { useState } from "react";
import { Link } from 'react-router-dom'
import { redirect } from "react-router-dom";
import { connect } from 'react-redux'
import { Alignment } from "docx";
import { login } from "../actions/auth";

function Login ({ login }) {
  const [formData, setFormData] = useState({
    email: '',
    password: ''
  });
  
  const { email, password } = formData;

  const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = e => {
    e.preventDefault();

    login(email, password);
};

  return (
    <div className="container mt-5">
      <h1 className="textalignleft">Sign In</h1>
      <p className="textalignleft">Sign into your Account</p>
      <form className="textalignleft" onSubmit={e => onSubmit(e)}>
        <div className='form-group'>
          <input 
            className="form-control"
            type='email'
            placeholder='Email'
            name='email'
            value={email}
            onChange={e => onChange(e)}
            required
            />
        </div>
        <br></br>
        <div className='form-group'>
          <input 
            className="form-control"
            type='password'
            placeholder='Password'
            name='password'
            value={password}
            onChange={e => onChange(e)}
            minLength='6'
            required
            />
        </div>
        <br></br>
        <button className="btnbtn-primary" type='submit'>Login</button>
      </form>
      <br></br>
      <p className='textalignleft'>
        Don't have an account? <Link to='/signup'>Sign Up</Link>
      </p>
      <p className='textalignleft'>
        Forgot Your Password? <Link to='/reset-password'>Reset Password</Link>
      </p>
    </div>
  );

};

// const mapStateToProps = state => ({

// })

export default connect(null, { login })(Login);